import React, { useState } from 'react';
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import 'prismjs/themes/prism-okaidia.css';
import 'prismjs/components/prism-json';
import { Button } from '@zendeskgarden/react-buttons';
import './App.css';
import Diagram from './Diagram/Diagram';

function App() {
  const [code, setCode] = useState(
    `{\r\n    "description": "Post Ticket Object to webhook",\r\n    "name": "{{integration_key}}",\r\n    "zis_template_version": "2019-10-14",\r\n    "resources": {\r\n        "ticket_created_jobspec": {\r\n            "type": "ZIS::JobSpec",\r\n            "properties": {\r\n                "name": "{{jobspec}}",\r\n                "event_source": "support",\r\n                "event_type": "{{jobspec_type}}",\r\n                "flow_name": "zis:{{integration_key}}:flow:{{jobspec}}_flow"\r\n            }\r\n        },\r\n        "{{jobspec}}_flow": {\r\n            "type": "ZIS::Flow",\r\n            "properties": {\r\n                "name": "{{jobspec}}_flow",\r\n                "definition": {\r\n                    "StartAt": "ZIS.LoadConfig",\r\n                    "States": {\r\n                        "ZIS.LoadConfig": {\r\n                            "Comment": "Load ZIS setting from config",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:action:LoadConfig",\r\n                            "Parameters": {\r\n                                "scope": "{{integration_key}}_settings"\r\n                            },\r\n                            "ResultPath": "$.config",\r\n                            "Next": "Zendesk.GetTicketData"\r\n                        },\r\n                        "Zendesk.GetTicketData": {\r\n                            "Comment": "Get all ticket data via API",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:{{integration_key}}:action:zendesk.get_ticket_object",\r\n                            "Parameters": {\r\n                                "ticketId.$": "{{$.input.ticket_event.ticket.id}}",\r\n                                "token.$": "{{$.connections.zendesk.access_token}}"\r\n                            },\r\n                            "ResultPath": "$.main_ticket",\r\n                            "Next": "Pass.InitiateTicketCommentsVariable"\r\n                        },\r\n                        "Pass.InitiateTicketCommentsVariable": {\r\n                            "Comment": "Initiate ticket_comments variable with an empty list",\r\n                            "Type": "Pass",\r\n                            "Result": [],\r\n                            "ResultPath": "$.ticket_comments",\r\n                            "Next": "Transform.InitiateGetCommentsEndpointVariable"\r\n                        },\r\n                        "Transform.InitiateGetCommentsEndpointVariable": {\r\n                            "Comment": "Initiate get_ticket_comments_url variable with an endpoint to get the first page",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:transform:Jq",\r\n                            "Parameters": {\r\n                                "expr": "\\"/api/v2/tickets/\\" + (.input.ticket_event.ticket.id | tostring) + \\"/comments\\"",\r\n                                "data.$": "$"\r\n                            },\r\n                            "ResultPath": "$.get_ticket_comments_url",\r\n                            "Next": "Zendesk.GetTicketComments"\r\n                        },\r\n                        "Zendesk.GetTicketComments": {\r\n                            "Comment": "Get all ticket comments via API",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:{{integration_key}}:action:zendesk.get_ticket_comments",\r\n                            "Parameters": {\r\n                                "url.$": "{{$.get_ticket_comments_url}}",\r\n                                "token.$": "{{$.connections.zendesk.access_token}}"\r\n                            },\r\n                            "ResultPath": "$.ticket_comments_response",\r\n                            "Next": "Transform.ConcatenateTicketCommentsResponse",\r\n                            "Catch": [\r\n                                {\r\n                                    "ErrorEquals": ["States.ALL"],\r\n                                    "ResultPath": "$.api_error_response",\r\n                                    "Next": "Transform.GetErrorCodeFromResponse"\r\n                                }\r\n                            ]\r\n                        },\r\n                        "Transform.ConcatenateTicketCommentsResponse": {\r\n                            "Comment": "Concatenate ticket comments response into ticket_comments variable and check if there is next page",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:transform:Jq",\r\n                            "Parameters": {\r\n                                "expr": ".ticket_comments + .ticket_comments_response.comments",\r\n                                "data.$": "$"\r\n                            },\r\n                            "ResultPath": "$.ticket_comments",\r\n                            "Next": "Choice.CheckIfThereIsNextPage"\r\n                        },\r\n                        "Choice.CheckIfThereIsNextPage": {\r\n                            "Comment": "Check if there is next page to execute Zendesk.GetTicketComments step passing the next_page url",\r\n                            "Type": "Choice",\r\n                            "Choices": [\r\n                                {\r\n                                    "Variable": "$.ticket_comments_response.next_page",\r\n                                    "IsPresent": true,\r\n                                    "Next": "Transform.StoreNextPageUrlAndCallGetTicketComments"\r\n                                }\r\n                            ],\r\n                            "Default": "Transform.StoreOutsideInfoInsideCommentObjects"\r\n                        },\r\n                        "Transform.StoreNextPageUrlAndCallGetTicketComments": {\r\n                            "Comment": "Store next page url into get_ticket_comments_url variable and execute Zendesk.GetTicketComments step again",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:transform:Jq",\r\n                            "Parameters": {\r\n                                "expr": ".ticket_comments_response.next_page",\r\n                                "data.$": "$"\r\n                            },\r\n                            "ResultPath": "$.get_ticket_comments_url",\r\n                            "Next": "Zendesk.GetTicketComments"\r\n                        },\r\n                        "Transform.StoreOutsideInfoInsideCommentObjects": {\r\n                            "Comment": "Include all data needed inside each ticket comment of the comments list to be used on the loop",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:transform:Jq",\r\n                            "Parameters": {\r\n                                "expr": ". as $main | [.ticket_comments[] | (.access_token=$main.connections.zendesk.access_token) | (.ticket_id=$main.main_ticket.ticket.id)]",\r\n                                "data.$": "$"\r\n                            },\r\n                            "ResultPath": "$.ticket_comments",\r\n                            "Next": "Map.LoopListOfComments"\r\n                        },\r\n                        "Map.LoopListOfComments": {\r\n                            "Comment": "Get evetns for each ticket comments",\r\n                            "Type": "Map",\r\n                            "ItemsPath": "$.ticket_comments",\r\n                            "ResultPath": "$.ticket_comments_response",\r\n                            "Iterator": {\r\n                                "StartAt": "Zendesk.GetTicketAuditById",\r\n                                "States": {\r\n                                    "Zendesk.GetTicketAuditById": {\r\n                                        "Comment": "Insert a public comment into the ticket",\r\n                                        "Type": "Action",\r\n                                        "ActionName": "zis:{{integration_key}}:action:zendesk.get_ticket_audit_by_id",\r\n                                        "Parameters": {\r\n                                            "ticketId.$": "{{$.ticket_id}}",\r\n                                            "auditId.$": "{{$.audit_id}}",\r\n                                            "token.$": "{{$.access_token}}"\r\n                                        },\r\n                                        "ResultPath": "$.audit_response",\r\n                                        "Next": "Transform.GetEventsFromAuditResponse"\r\n                                    },\r\n                                    "Transform.GetEventsFromAuditResponse": {\r\n                                        "Comment": "Get events from audit response but if the events attribute is null it will return a empty list",\r\n                                        "Type": "Action",\r\n                                        "ActionName": "zis:common:transform:Jq",\r\n                                        "Parameters": {\r\n                                            "expr": ".audit_response.audit.events // []",\r\n                                            "data.$": "$"\r\n                                        },\r\n                                        "ResultPath": "$.events",\r\n                                        "Next": "Done.LoopListOfComments"\r\n                                    },\r\n                                    "Done.LoopListOfComments": {\r\n                                        "Type": "Succeed"\r\n                                    }\r\n                                }\r\n                            },\r\n                            "Next": "Transform.CheckLoopResponse"\r\n                        },\r\n                        "Transform.CheckLoopResponse": {\r\n                            "Comment": "Check if response from loop is null",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:transform:Jq",\r\n                            "Parameters": {\r\n                                "expr": ".ticket_comments_response == null",\r\n                                "data.$": "$"\r\n                            },\r\n                            "ResultPath": "$.is_loop_response_null",\r\n                            "Next": "Choice.CheckIfLoopResponseIsNull"\r\n                        },\r\n                        "Choice.CheckIfLoopResponseIsNull": {\r\n                            "Comment": "Check if loop response is null to get the events or skip that step",\r\n                            "Type": "Choice",\r\n                            "Choices": [\r\n                                {\r\n                                    "Variable": "$.is_loop_response_null",\r\n                                    "BooleanEquals": true,\r\n                                    "Next": "External.PostTicketObject"\r\n                                }\r\n                            ],\r\n                            "Default": "Transform.InsertEventsInsideTicketComments"\r\n                        },\r\n                        "Transform.InsertEventsInsideTicketComments": {\r\n                            "Comment": "Check if response from loop is null",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:transform:Jq",\r\n                            "Parameters": {\r\n                                "expr": ".ticket_comments[] as $comment | $comment + {events: [.ticket_comments_response[] | select(.id == $comment.id).events]}",\r\n                                "data.$": "$"\r\n                            },\r\n                            "ResultPath": "$.ticket_comments",\r\n                            "Next": "External.PostTicketObject"\r\n                        },\r\n                        "External.PostTicketObject": {\r\n                            "Comment": "Post ticket object and ticket comments list to the external webhook",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:{{integration_key}}:action:post-to-webhook-site",\r\n                            "Parameters": {\r\n                                "endpoint.$": "$.config.webhook_endpoint",\r\n                                "ticketObject.$": "$.main_ticket",\r\n                                "ticketComments.$": "$.ticket_comments"\r\n                            },\r\n                            "Next": "Done.Success"\r\n                        },\r\n                        "Transform.GetErrorCodeFromResponse": {\r\n                            "Comment": "Check if api response has 422 http error",\r\n                            "Type": "Action",\r\n                            "ActionName": "zis:common:transform:Jq",\r\n                            "Parameters": {\r\n                                "expr": ".Cause | contains(\\"404\\")",\r\n                                "data.$": "$.api_error_response"\r\n                            },\r\n                            "ResultPath": "$.api_422_error_code",\r\n                            "Next": "Choice.ValidateErrorCode"\r\n                        },\r\n                        "Choice.ValidateErrorCode": {\r\n                            "Comment": "If a 422 http error happened log an specific error or log a generic one",\r\n                            "Type": "Choice",\r\n                            "Choices": [\r\n                                {\r\n                                    "Variable": "$.api_422_error_code",\r\n                                    "BooleanEquals": true,\r\n                                    "Next": "Fail.UnprocessableEntityError"\r\n                                }\r\n                            ],\r\n                            "Default": "Fail.GenericError"\r\n                        },\r\n                        "Fail.UnprocessableEntityError": {\r\n                            "Type": "Fail",\r\n                            "Cause": "Unprocessable Entity Error: {{$.api_error_response.Cause}}",\r\n                            "Error": "{{$.api_error_response}}"\r\n                        },\r\n                        "Fail.GenericError": {\r\n                            "Type": "Fail",\r\n                            "Cause": "{{$.api_error_response.Cause}}",\r\n                            "Error": "{{$.api_error_response}}"\r\n                        },\r\n                        "Done.Success": {\r\n                            "Type": "Succeed",\r\n                            "Message": "#{{$.input.ticket_event.ticket.id}} => Execution Successful"\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        },\r\n        "zendesk.get_ticket_object": {\r\n            "type": "ZIS::Action::Http",\r\n            "properties": {\r\n                "name": "zendesk.get_ticket_object",\r\n                "definition": {\r\n                    "method": "GET",\r\n                    "path": "/api/v2/tickets/{{$.ticketId}}.json",\r\n                    "headers": [\r\n                        {\r\n                            "key": "Content-Type",\r\n                            "value": "application/json"\r\n                        },\r\n                        {\r\n                            "key": "Authorization",\r\n                            "value": "Bearer {{$.token}}"\r\n                        }\r\n                    ]\r\n                }\r\n            }\r\n        },\r\n        "zendesk.get_ticket_comments": {\r\n            "type": "ZIS::Action::Http",\r\n            "properties": {\r\n                "name": "zendesk.get_ticket_comments",\r\n                "definition": {\r\n                    "method": "GET",\r\n                    "path": "{{$.url}}",\r\n                    "headers": [\r\n                        {\r\n                            "key": "Content-Type",\r\n                            "value": "application/json"\r\n                        },\r\n                        {\r\n                            "key": "Authorization",\r\n                            "value": "Bearer {{$.token}}"\r\n                        }\r\n                    ]\r\n                }\r\n            }\r\n        },\r\n        "zendesk.get_ticket_audit_by_id": {\r\n            "type": "ZIS::Action::Http",\r\n            "properties": {\r\n                "name": "zendesk.get_ticket_audit_by_id",\r\n                "definition": {\r\n                    "method": "GET",\r\n                    "path": "/api/v2/tickets/{{$.ticketId}}/audits/{{$.auditId}}.json",\r\n                    "headers": [\r\n                        {\r\n                            "key": "Content-Type",\r\n                            "value": "application/json"\r\n                        },\r\n                        {\r\n                            "key": "Authorization",\r\n                            "value": "Bearer {{$.token}}"\r\n                        }\r\n                    ]\r\n                }\r\n            }\r\n        },\r\n        "post-to-webhook-site": {\r\n            "type": "ZIS::Action::Http",\r\n            "properties": {\r\n                "name": "post-to-webhook-site",\r\n                "definition": {\r\n                    "method": "POST",\r\n                    "url": "{{$.endpoint}}",\r\n                    "requestBody": {\r\n                        "ticket_data": "{{$.ticketObject}}",\r\n                        "ticket_comments": "{{$.ticketComments}}"\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}`
  );
  const [parsedBundle, setParsedBundle] = useState(null);
  const [actions, setActions] = useState(null);

  const listActions = (rawJSON) => {
    setActions(null);
    let parsedJSON = {};
    try {
      parsedJSON = JSON.parse(rawJSON);
      setParsedBundle(parsedJSON);
    } catch (error) {
      console.error(error);
      return;
    }

    if (!parsedJSON.resources) return;

    const bundleFlow = Object.values(parsedJSON.resources).find(resource => resource.type === 'ZIS::Flow');
    const actions = bundleFlow.properties?.definition?.States;
    console.log('actions', Object.values(actions));
    setActions(actions)
  }
  return (
    <div className="c-container">
      <h1>ZIS Bundle Viewer</h1>
  
      <p>Paste your bundle JSON below</p>
      
      <Editor
        value={code}
        onValueChange={code => setCode(code)}
        highlight={code => highlight(code, languages.js)}
        padding={10}
        style={{
          fontFamily: '"Fira code", "Fira Mono", monospace',
          fontSize: 14,
          backgroundColor: '#272822',
          maxHeight: '500px',
          overflow: 'auto'
        }}
      />

      <Button 
        isPrimary 
        className="c-btn--submit"
        onClick={() => listActions(code)}>
        Submit
      </Button>

      {actions &&
        <div>
          <p>{parsedBundle.description}</p>

          <Diagram actions={actions}></Diagram>
        </div>
      }
    </div>
  );
}

export default App