import React, { useRef, useState } from 'react';
import OrganizationChart from '@dabeng/react-orgchart';
import MyNode from './DiagramNode';
import { Button } from '@zendeskgarden/react-buttons';

function Diagram(props) {
  const orgchart = useRef();

  const [actionsTree, setActionsTree] = useState({});
  let currentId = 0;
  
  const objectDepth = (o) =>
    Object (o) === o ? 1 + Math.max (-1, ...Object.values(o).map (objectDepth)) : 0;

  const buildSequenceFirstLevel = (actions) => {
    // If you get object values, actions come ir order
    // If you access the object directly, it will alphabetically order attributes
    const actionsInOrder = Object.values(actions);
    const firstAction = JSON.stringify(actionsInOrder[0]);
    let firstActionObject = {}

    for (let action in actions) {
      if (JSON.stringify(actions[action]) === firstAction) {
        firstActionObject = {title: action,...actions[action]};
        break;
      }
    }

    return {
      id: ++currentId,
      title: firstActionObject.title,
      description: firstActionObject.Comment || 'Action',
      preview: JSON.stringify(firstActionObject, null, 2),
      children: [
        { id: ++currentId, title: firstActionObject.Next }
      ]
    }
  };

  const prepareChildObject = (child, childCorrespondingAction, actions) => {
    child.children = [];
    child.description = childCorrespondingAction.Comment || childCorrespondingAction.Error || childCorrespondingAction.Message || 'Action';
    child.preview = JSON.stringify(childCorrespondingAction, null, 2);

    // Next
    if (childCorrespondingAction.Next) {
      const nextStepObject = actions[childCorrespondingAction.Next];
      child.children.push({
        id: ++currentId,
        title: childCorrespondingAction.Next,
        description: nextStepObject.Comment || nextStepObject.Error || nextStepObject.Message || 'Action',
        preview: JSON.stringify(nextStepObject, null, 2),
      })
    }
    // Catch
    if (childCorrespondingAction.Catch) {
      const nextStepObject = actions[childCorrespondingAction.Catch[0].Next];
      child.children.push({
        id: ++currentId,
        title: childCorrespondingAction.Catch[0].Next,
        description: nextStepObject.Comment || nextStepObject.Error || nextStepObject.Message || 'Action',
        preview: JSON.stringify(nextStepObject, null, 2),
      })
    }
    // Choices
    if (childCorrespondingAction.Choices) {
      childCorrespondingAction.Choices.forEach(choice => {
        const nextStepObject = actions[choice.Next];
        child.children.push({
          id: ++currentId,
          title: choice.Next,
          description: nextStepObject.Comment || nextStepObject.Error || nextStepObject.Message || 'Action',
        preview: JSON.stringify(nextStepObject, null, 2),
        })
      })
    }
    // Default
    if (childCorrespondingAction.Default) {
      const nextStepObject = actions[childCorrespondingAction.Default];
      child.children.push({
        id: ++currentId,
        title: childCorrespondingAction.Default,
        description: nextStepObject.Comment || nextStepObject.Error || nextStepObject.Message || 'Action',
        preview: JSON.stringify(nextStepObject, null, 2),
      })
    }

    if (!childCorrespondingAction.timesVerified) childCorrespondingAction.timesVerified = 1;
    else childCorrespondingAction.timesVerified++;
  };

  const buildSequence = (actions) => {
    let higherDepthObject = {};
    if (!actions) return {};

    const sequence = buildSequenceFirstLevel(actions);

    const goThroughActions = (children, actions) => {
      children.forEach(child => {
        const childCorrespondingAction = actions[child.title];
        prepareChildObject(child, childCorrespondingAction, actions);

        if (objectDepth(sequence) > objectDepth(higherDepthObject)) {
          higherDepthObject = JSON.parse(JSON.stringify(sequence));
        }
        console.log(objectDepth(higherDepthObject));

        if (child.children.length === 0) {
          delete child.children;
        }
      });

      // If has children, loop through them
      children.forEach(child => {
        const childCorrespondingAction = actions[child.title];
        if (child.children?.length > 0 && childCorrespondingAction.timesVerified < 20) {
          goThroughActions(child.children, actions);
        }
      });
    }

    goThroughActions(sequence.children, actions);
    console.log(higherDepthObject)
    return higherDepthObject;
  };


  const sequence = buildSequence(props.actions);
  if (objectDepth(sequence) > objectDepth(actionsTree)) {
    setActionsTree(sequence);
  }

  const exportTo = (extension = 'png') => {
    orgchart.current?.exportTo('bundle_view', extension);
  };

  return (
    <div>
      <Button 
        isPrimary 
        className="c-btn--download"
        onClick={() => exportTo('png')}>
        PNG
      </Button>
      <Button 
        isPrimary 
        className="c-btn--download pdf"
        onClick={() => exportTo('pdf')}>
        PDF
      </Button>

      <OrganizationChart
        datasource={actionsTree}
        chartClass="myChart"
        zoom={true}
        ref={orgchart}
        NodeTemplate={MyNode}
      />
    </div>
  );
}

export default Diagram